<template>
    <el-container>
        <el-main>
            <el-form :model="saveForm" ref="saveForm">
                <el-table
                    :data="saveForm.tData"
                    style="width: 100%"
                    class="table-eventLevel"
                >
                    <el-table-column width="120" prop="">
                        <template slot-scope="scope">
                            <span style="vertical-align: middle"
                                >{{ scope.row.level }}.{{
                                    levelName[scope.row.level]
                                }}</span
                            >
                        </template>
                    </el-table-column>

                    <el-table-column prop="" label="(A)几乎不发生" width="150">
                        <template slot-scope="scope">
                            <el-select
                                v-model="scope.row.A"
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="item in options"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value"
                                >
                                </el-option>
                            </el-select>
                        </template>
                    </el-table-column>
                    <el-table-column prop="" label="(B)很少发生" width="150"
                        ><template slot-scope="scope">
                            <el-select
                                v-model="scope.row.B"
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="item in options"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value"
                                >
                                </el-option>
                            </el-select> </template></el-table-column
                    ><el-table-column prop="" label="(C)偶尔发生" width="150"
                        ><template slot-scope="scope">
                            <el-select
                                v-model="scope.row.C"
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="item in options"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value"
                                >
                                </el-option>
                            </el-select> </template
                    ></el-table-column>
                    <el-table-column prop="" label="(D)可能发生" width="150"
                        ><template slot-scope="scope">
                            <el-select
                                v-model="scope.row.D"
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="item in options"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value"
                                >
                                </el-option>
                            </el-select> </template
                    ></el-table-column>

                    <el-table-column prop="" label="(E)经常发生" width="150"
                        ><template slot-scope="scope">
                            <el-select
                                v-model="scope.row.E"
                                placeholder="请选择"
                            >
                                <el-option
                                    v-for="item in options"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value"
                                >
                                </el-option>
                            </el-select> </template
                    ></el-table-column>
                </el-table>
            </el-form>
            <el-button
                type="primary"
                style="margin-top: 15px"
                @click.native="saveSubmit"
                :loading="saveLoading"
                >保存</el-button
            >
        </el-main>
    </el-container>
</template>
<script>
import { setplalgorithm, getplalgorithm } from "../../api/api";
export default {
    data() {
        return {
            saveForm: {
                tData: [],
            },
            levelName: {
                1: "轻微的",
                2: "较小的",
                3: "较大的",
                4: "重大的",
                5: "特大的",
                6: "灾难的",
            },
            saveLoading: false,
            options: [
                {
                    value: 1,
                    label: "低",
                },
                {
                    value: 2,
                    label: "中",
                },
                {
                    value: 3,
                    label: "高",
                },
                {
                    value: 4,
                    label: "超高",
                },
            ],
            saveSubmit: function () {
                let _this = this;

                this.saveLoading = true;
                let para = Object.assign({}, this.saveForm);
                setplalgorithm(para).then((res) => {
                    if (res.success) {
                        this.$message({
                            message: "设置成功",
                            type: "success",
                        });

                        this.getAllData();
                    } else {
                        this.$message({
                            message: res.msg,
                            type: "error",
                        });
                    }
                    this.saveLoading = false;
                });
            },
        };
    },
    methods: {
        getAllData() {
            getplalgorithm().then((res) => {
                if (res.success) {
                    this.saveForm.tData = res.data;
                }
            });
        },
    },
    mounted() {
        this.getAllData();
    },
};
</script>
<style lang="less" scoped>
.el-table .cell {
    white-space: pre-line;
}
.table-eventLevel {
    /deep/ thead th {
        background: #fff;
        padding: 37px 0;
    }

    /deep/ thead tr:first-of-type th:first-of-type:before {
        content: "严重度";
        text-align: center;
        position: absolute;
        width: 152px;
        height: 1px;
        bottom: 30px;
        right: 0;
    }

    /deep/ thead tr:first-of-type th:first-of-type:after {
        content: "可能性";
        text-align: center;
        position: absolute;
        width: 152px;
        top: 10px;
        left: 0;
    }

    /deep/ thead tr:first-of-type th:first-of-type .cell {
        position: absolute;
        top: 0;
        left: 0;
        width: 152px;
        height: 1px;
        background-color: #ebeef5;
        display: block;
        text-align: center;
        transform: rotate(38deg);
        transform-origin: top left;
        -ms-transform: rotate(38deg);
        -ms-transform-origin: top left;
        -webkit-transform: rotate(38deg);
        -webkit-transform-origin: top left;
    }
}
</style>


